.App {
  text-align: center;
}

body {
  font-family:'Courier New', Courier, monospace
}


.title {
  background: white;
  width: 100%;
  display: flex;
  flex-direction: column; /* 縦に並べる */
  color: #4682B4; /* タイトルの色 */
  padding: 25px;
  font-weight: 600; /* pxは不要 */
  font-size: 40px; /* タイトルのサイズ */
  position: relative; /* 相対位置を設定 */
  margin: 10px 0; /* 上下に10pxの空間を追加 */
  text-align: center;

}
.sumai {
  font-size: 40px;
  text-align: center;
}


.top-menu {
  margin-top: 2px; /* 上のスペースを調整 */
  text-align: right; /* 中央に配置 */
}

.top-menu-item {
  margin: 5px 0; /* 各項目の間隔を調整 */
  color: black; /* 色を黒に設定 */
  font-size: 24px; /* サイズを小さく設定 */
}

.title::after {
  content: ""; /* 空のコンテンツを追加 */
  display: block; /* ブロック要素として表示 */
  width: 100%; /* 幅を100%に設定 */
  height: 2px; /* 高さを2pxに設定 */
  background-color: gray; /* 線の色を灰色に設定 */
  position: absolute; /* 絶対位置に設定 */
  bottom: -2px; /* 下に2pxの空間を設ける */
}




.menu {
  display: flex;
  flex-direction: column; /* 縦に配置 */
  align-items: center; /* 中央に揃える */
}

.menu button {
  width: 180px; /* ボタンの幅を指定 */
  height: 50px; /* ボタンの高さを指定 */
  margin: 5px 0; /* ボタンの上下間隔を調整 */
  font-size: 16px; /* フォントサイズを調整 */
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column; /* 縦に並べる */
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
